import React,{Fragment} from 'react'
import {Button} from 'antd'
import './index.css'
import PubSub from 'pubsub-js'
import {Link} from 'react-router-dom'
import navBtn from '../../common/img/navBtn.png'
export default function Header() {
  let [HasLogin,SetHasLogin]=React.useState(false)
  let [UserName,SetUserName]=React.useState('')
  let [hasNav,setHasNav]=React.useState(false)
  const HandleHasLogin=(mas,data)=>{
    SetHasLogin(data)
  }
  const HandleUserName=(mas,data)=>{
    SetUserName(data)
  }
  const offLogin=()=>{
    PubSub.publish('HasLogin',false)
  }
  const clickNav=()=>{
    // // 字符串0.5
    // document.getElementsByTagName('body')[0].style.opacity='0.6'
    if(!hasNav){
      PubSub.publish('navHeight','45vh')
    }else{
      PubSub.publish('navHeight','0')
    }
    setHasNav(!hasNav)
  }
  React.useEffect(()=>{
    PubSub.subscribe('HasLogin',HandleHasLogin)
    PubSub.subscribe('UserName',HandleUserName)
  })
    return (
      <Fragment>
        {/* 移动端占位空间 */}
      <div className='header-space'></div>
        <div className="header">
        {/* 只是为了让页面加载antd的默认样式 */}
        <Button type="primary" className='header-antdDefault'>Button</Button>  
      <span className="header-title">Aays's blog</span>
      <div className='header-navBtn' onClick={clickNav}><img src={navBtn} className='header-navBtnImg'/></div>
        {
          HasLogin?
          (<div className='header-user'><div className='header-username'>Hello {UserName}<span><Button type="primary" style={{height:'5vh',backgroundColor:'red',borderRadius:'25px',border:'1px solid red',marginLeft:'1vw',position:'relative',bottom:'0.6vh'}} onClick={offLogin}>
          退出登录
          </Button>  </span></div></div>)
          : 
          (<div className='header-user'><Link to='/user/login'>
          <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',border:'none'}}>Sign in</Button>  
         </Link>
         <Link to='/user/register'> 
         <Button type="primary" style={{height:'5vh',backgroundColor:'rgba(0,0,0,0)',borderRadius:'25px',border:'1px solid white'}}>
           Sign up
           </Button>  
          </Link> </div>)
        }
  </div>
  </Fragment>
    )
}
